import React, { Component } from 'react'
import SkinContainer from "./SkinContainer";
class MainPage extends Component {
    constructor(props) {
        super(props)
        this.state = {
            currentSearch:""
        }
    }
    updateSeries = (e) => {
        this.setState({
            currentSearch: e.target.value
        })
    }

    render() {
        return(
            <div className="view">
                <div className="series">
                    <div className="row">
                        <input type="text" className="searchInput wm-L1" placeholder="Skin name" onBlur={this.updateSeries}/>
                        <button 
                            className="searchBtn wm-L1"
                            onClick={this.props.search.bind(this,this.state.currentSearch)}
                        >
                            üîç
                        </button>
                    </div>
                </div>
                <SkinContainer
                    dontRandomize={true}
                    search={this.props.searchName}
                    elId={"gallery"} 
                    skins={this.props.skins}
                    addSkin={this.props.addSkin}
                    type={this.props.type}
                    removeSkin={this.props.removeSkin}
                />
            </div>
        )
    }
}

export default MainPage