import React, { Component } from 'react'
import ReactDOM from 'react-dom';
import SwipeableViews from 'react-swipeable-views';
import './specy.css';
import './index.css';
import Mainpage from './Components/mainPage'
import SecondPage from './Components/secondPage'
import ThirdPage from './Components/thirdPage'
class App extends Component {
  constructor() {
    super()
    let savedSkins = localStorage.getItem("savedSkins") === null ? [] : JSON.parse(localStorage.getItem("savedSkins"))
    this.state = {
      loading:true,
      skins: [],
      pageIndex: 0,
      savedSkins: savedSkins,
      searchSkin: "",
      searchSkins: []
    }
    this.getSkins()
  }
  getSkins = async () => {
    let skins = await fetch("https://cors-anywhere.herokuapp.com/https://gamedata.gota.io/skinData.php").then(data => data.json())
    skins = JSON.parse(skins.names)
    this.setState({
      skins: skins,
      loading:false
    })
    console.log("Fetched skins")
  }
  addskin = (skin, e) => {
    let skins = this.state.savedSkins
    if(skins.includes(skin)) return
    skins.push(skin)
    localStorage.setItem("savedSkins", JSON.stringify(skins))
    this.setState({
      savedSkins: skins
    })
  }
  removeSkin = (skin) => {
    let skins = this.state.savedSkins
    let index = skins.indexOf(skin)
    skins.splice(index, 1)
    localStorage.setItem("savedSkins", JSON.stringify(skins))
    this.setState({
      skins: skins
    })
  }
  search = (skinName) => {
    let skins = [skinName]
    for (let i = 1; i < 500; i++) {
      skins.push(skinName + i)
    }
    skins = skins.filter(skin => this.state.skins.includes(skin))
    this.setState({
      searchSkin: skinName,
      searchSkins: skins
    })
  }
  changePageIndex = (index) => {
    this.setState({
      pageIndex: index,
    });
  };
  render() {

    return (
      <div>
        <div className="navbar dm-L3">
          <button className="navItem dm-L1" onClick={this.changePageIndex.bind(this, 0)}>Discover</button>
          <button className="navItem dm-L1" onClick={this.changePageIndex.bind(this, 1)}>Gallery</button>
          <button className="navItem dm-L1" onClick={this.changePageIndex.bind(this, 2)}>Your skins</button>
        </div>
        {this.state.loading ? <div className="title text-white">Loading...</div>: <div></div>}
        <SwipeableViews index={this.state.pageIndex} onChangeIndex={this.changePageIndex} className="slider">
          <Mainpage skins={this.state.searchSkins} search={this.search} searchName={this.state.searchSkin} addSkin={this.addskin} type="gallery" />
          <SecondPage skins={this.state.skins} addSkin={this.addskin} type="gallery" />
          <ThirdPage skins={this.state.savedSkins} removeSkin={this.removeSkin} type="savedSkins" />
        </SwipeableViews>
      </div>

    )
  }
}

// ========================================

ReactDOM.render(< App />, document.getElementById("root"));